<template>
  <div>
    <el-row style="margin-bottom: 10px">
      <el-col :span="12">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item ><el-button size="medium" type="danger" round></el-button></el-breadcrumb-item>
          <el-breadcrumb-item ><el-button size="medium" type="danger" round></el-button></el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
      <el-col :span="8" :offset="4">
        <el-button @click="dialog = true" type="primary" size="medium"></el-button>
        <el-button @click="findAllPeoples(1 )" type="success" size="medium"></el-button>
      </el-col>
    </el-row>
    <el-table :data="peoples">
      <el-table-column label="" prop="code">
      </el-table-column>
      <el-table-column label="" prop="location">
      </el-table-column>
      <el-table-column label="" prop="date">
      </el-table-column>
      <el-table-column label="" prop="number">
      </el-table-column>
      <el-table-column label="" prop="reporting_unit">
      </el-table-column>
      <el-table-column fixed="right" label="">
        <template slot-scope="scope">
          <el-button @click="deletePeople(scope.row)" type="danger" size="small"></el-button>
          <el-button type="primary" size="small" @click="editPeople(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-row>
      <el-col :span="8" :offset="8">
        <!---->
        <el-pagination style="margin: 15px 0px;"
                       background
                       prev-text=""
                       next-text=""
                       layout="prev, pager, next, total, jumper"
                       @current-change="findPageInfo"
                       :current-page="pageNow"
                       :total="total">
          <!--                        total -->
        </el-pagination>
      </el-col>
    </el-row>

    <!---->
    <el-dialog title="" :visible.sync="dialogFormVisible" center>
      <el-form :model="editForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.code" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.location" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.date" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.number" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="editForm.reporting_unit" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveInfo"> </el-button>
        <el-button @click="dialogFormVisible = false"> </el-button>
      </div>
    </el-dialog>

    <!--    -->
    <el-drawer
      title=", "
      :before-close="handleClose"
      :visible.sync="dialog"
      direction="ltr"
      custom-class="demo-drawer"
      ref="drawer"
    >
      <div class="demo-drawer__content">
        <el-form :model="searchForm">
          <el-form-item label="" :label-width="searchFormLabelWidth">
            <el-input v-model="searchForm.code" autocomplete="off" style="width: 80%" minlength="0"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="searchFormLabelWidth">
            <el-input v-model="searchForm.location" autocomplete="off" style="width: 80%" minlength="0"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="searchFormLabelWidth">
            <el-input v-model="searchForm.date" autocomplete="off" style="width: 80%" minlength="0"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="searchFormLabelWidth">
            <el-input v-model="searchForm.number" autocomplete="off" style="width: 80%" minlength="0"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="searchFormLabelWidth">
            <el-input v-model="searchForm.reporting_unit" autocomplete="off" style="width: 80%"
                      minlength="0"></el-input>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button @click="cancelForm"> </el-button>
          <el-button type="primary" @click="$refs.drawer.closeDrawer()" :loading="loading">{{ loading ? ' ...' : ''
            }}
          </el-button>
        </div>
      </div>
    </el-drawer>


  </div>
</template>

<script>
  export default {
    name: "People",
    data() {
      return {
        total: 10,
        pageNow: 1,
        //start
        dialog: false,
        loading: false,
        searchForm: {
          code: null,
          location: null,
          date: null,
          number: null,
          reporting_unit: null,
        },
        searchFormLabelWidth: '80px',
        timer: null,
        //end


        //start
        dialogFormVisible: false,
        formLabelWidth: '120px',
        editForm: {
          id: null,
          code: "",
          location: "",
          date: "",
          number: "",
          reporting_unit: ""
        },
        //end

        //
        peoples: []
        //end
      }
    },
    methods: {
      successResult() {
        this.$message({
          message: '!',
          type: 'success'
        });
      },
      failResult() {
        this.$message.error('!');
      },

      /**
       * 
       * @param rowData
       */
      deletePeople(rowData) {
        //
        console.log(rowData);
        this.$http.post("/api/people/deleteOne/" + rowData.id)
          .then(res => {
            if (res.data.status) {
              this.successResult();
              this.findAllPeoples();
            } else {
              this.failResult();
            }
          })
      },

      //
      editPeople(rowData) {
        this.dialogFormVisible = true;
        this.editForm.code = rowData.code;
        this.editForm.location = rowData.location;
        this.editForm.number = rowData.number;
        this.editForm.date = rowData.date;
        this.editForm.reporting_unit = rowData.reporting_unit;
        this.editForm.id = rowData.id;
      },

      //
      findAllPeoples(page) {
        if (page === undefined) {
          page = this.pageNow
        }
        this.$http.post("/api/people/page/" + page)
          .then(
            res => {
              this.peoples = res.data.onePageContent;
              this.total = res.data.totalElements;
            }
          )
      },


      //
      findPageInfo(page) {
        this.findAllPeoples(page);
        console.log(page)
      },

      //
      saveInfo() {
        this.dialogFormVisible = false;
        //
        this.$http.post("/api/people/updateOne", this.editForm)
          .then(res => {
            if (res.data.status) {
              this.successResult();
              this.findAllPeoples();
            } else {
              this.failResult();
            }
          })
        //
      },

      //
      searchContent() {
        this.$http.post("/api/people/findAllWithSpecification", this.searchForm)
          .then(res => {
            // this.peoples = res.data;
            this.peoples = res.data;
          })
        this.$refs.drawer.closeDrawer();
      },

      //start
      handleClose(done) {
        if (this.loading) {
          return;
        }
        this.$confirm('')
          .then(_ => {
            this.loading = true;
            this.timer = setTimeout(() => {
              this.searchContent();
              done();
              // 
              setTimeout(() => {
                this.loading = false;
              }, 400);
            }, 2000);
          })
          .catch(_ => {
          });
      },
      cancelForm() {
        this.loading = false;
        this.dialog = false;
        clearTimeout(this.timer);
      },
      //end
    },
    created() {
      this.findAllPeoples();
      this.findPageInfo();
    }
  }
</script>

<style scoped>

</style>



