<template>
  <div class="login_container">
    <div>

    </div>
    <div class="login_box">
      <!--  -->
      <div>
        <el-form
          ref="loginFormRef"
          :model="loginForm"
          :rules="loginFormRules"
          label-width="60px"
          class="login_form"
        >
          <el-form-item label="" prop="username">
            <el-input v-model="loginForm.username" prefix-icon="iconfont icon-user"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password" >
            <el-input
              v-model="loginForm.password"
              type="password"
              prefix-icon="iconfont icon-3702mima"
              show-password
            ></el-input>
          </el-form-item>
          <el-form-item class="btns">
            <el-button type="primary" @click="login"></el-button>
            <el-button type="info" @click="resetLoginForm"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
  import aes from "../../utils/aes";
  export default {
    name: "Login",
    data() {
      return {
        loginForm: {
          username: 'admin',
          password: '123456'
        },
        loginFormEncrypt: {
          username: "",
          password: "",
        },
        // 
        loginFormRules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
            {min: 2, max: 10, message: ' 2  10 ', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 18, message: ' 6  18 ', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      // 
      resetLoginForm() {
        // console.log(this)
        // resetFieldselement-ui
        this.$refs.loginFormRef.resetFields();
      },
      login() {
        // 
        // validbool
        this.$refs.loginFormRef.validate(async valid => {
          // console.log(valid)
          if (!valid) return false
          // this.$http.post('login', this.loginForm): promise
          // promiseawait async
          this.loginFormEncrypt.username = this.loginForm.username;
          this.loginFormEncrypt.password = aes.encrypt(this.loginForm.password);
          const {data: res} = await this.$http.post('/administrator/login', this.loginFormEncrypt)
          // const result = await this.$http.post('http://localhost:9191/login', this.loginForm)
          console.log(res)
          if (res.meta.status) return this.$message.error(',')
          this.$message.success('')
          // // 1token, sessionStorage; localStorage
          // //   1.1 API
          // //   1.2 token tokensessionStorage
          console.log(res.token);
          window.sessionStorage.setItem('token', res.token)
          // // 2, /home
          this.$router.push('/layout/systemcenter/ordinuser')
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  /* // lang="less" less
  // scoped vue */

  .login_container {
    background-color: #185ea3;
    height: 100%;
  }

  .login_box {
    width: 450px;
    height: 360px;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    border: 2px solid black;
  }


  .login_form {
    position: absolute;
    bottom: 60px;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }

  .btns {
    display: flex;
    justify-content: center;
  }

  .info {
    font-size: 13px;
    margin: 10px 15px;
  }
</style>



